import { WatchlistDataTablePercentageCell } from "../../WatchlistDataTableCells/WatchlistDataTablePercentageCell"
import { genericColumn } from "../../genericColumn"

function returnOnCapitalEmployedTtm(tableName: string) {
  const title = "ROCE (LTM)"
  return genericColumn({
    endpoint: "companyKeyMetrics",
    columnKeys: ["returnOnCapitalEmployedTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Profit from capital employed.",
    cell: info => {
      return (
        <WatchlistDataTablePercentageCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyKeyMetrics}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about return on capital employed or other relevant financial measures?`,
              value:
                info.row.original.financialData.companyKeyMetrics?.data?.returnOnCapitalEmployedTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            (info.row.original.financialData.companyKeyMetrics?.data?.returnOnCapitalEmployedTtm ??
              0) * 100
          }
        />
      )
    },
  })
}

export { returnOnCapitalEmployedTtm }
